<template>
    <div class="home container p-3 mx-auto">
          <div class="row">
                <div class="col-md-3 d-none d-md-block ">
                       <img class="logo" src="@/assets/KWorkTech_new_logo.jpg">
                </div>

             
                <div class="col-md-9">
                                    <!-- <header-options
                                      v-bind:in_data="headerOptions">
                                    </header-options>   -->
                <carousel  :autoplay="true" :nav="false" :responsive="{0:{items:1,nav:false},600:{items:3,nav:false}}">
                    <div class="p-2" v-if="productList.new[0]" >
                          <app-product-card    v-bind:in_data="productList.new[0]" :watchList="productWatchList" v-on:addProductList = "addProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" v-if="productList.new[1]" >
                          <app-product-card    v-bind:in_data="productList.new[1]" :watchList="productWatchList" v-on:addProductList = "addProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" v-if="productList.new[2]" >
                          <app-product-card    v-bind:in_data="productList.new[2]" :watchList="productWatchList" v-on:addProductList = "addProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" v-if="productList.new[3]" >
                          <app-product-card     v-bind:in_data="productList.new[3]" :watchList="productWatchList" v-on:addProductList = "addProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" v-if="productList.new[4]" >
                          <app-product-card    v-bind:in_data="productList.new[4]" :watchList="productWatchList" v-on:addProductList = "addProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" v-if="productList.new[5]" >
                          <app-product-card   v-bind:in_data="productList.new[5]" :watchList="productWatchList" v-on:addProductList = "addProductWatchList"></app-product-card>
                    </div> 
                </carousel> 
                </div> 
          </div>
          <div class="row border-top border-bottom border-dark font-weight-bold" style="display:flow-root">
              <div class="top-menu">
                <app-sm-side-nav class="d-block d-md-none pl-3" v-on:callProductList = "getProductList"></app-sm-side-nav>
                 <div class="offset-md-3 p-2 watchlist-block" data-toggle="modal" data-target="#isProductWatchList" v-if="productWatchList.length != 0"> 
                    <div class="watchlist watchlist-exist">
                      <a href="javascript:void(0)"><span> My Collection </span> <span class="watchlistcount pl-2">{{this.productWatchList.length}}</span></a>
                    </div> 
                  </div>
                  <div class="offset-md-3 p-2 watchlist-block" v-if="productWatchList.length == 0">
                    <div class="watchlist watchlist-none">
                      <span>My Collection </span> 
                    </div>
                  </div> 
                  </div>
          </div>
          
          <div class="modal fade" id="isProductWatchList" role="dialog">
            <div class="modal-dialog modal-md">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                        <h4 class="modal-title">Product WatchList </h4>
                    </div>
                    <div class="modal-body">
                          <div class="template-body tab-bg pt-2">
                              <div class="table-responsive pt-2">
                                  <table  class="table border pt-2 text-uppercase">
                                    <tbody  >
                                        <tr>
                                        <th>S No.</th>
                                        <th>Brand Name</th>
                                        <th>Product Name</th>
                                        <th>2D File</th>
                                        <th>3D File</th>
                                        <th>PDF</th>
                                        </tr>
                                    </tbody>
                                    <tbody>
                                        <tr  v-for="(product,index) in productWatchList" :key="index">
                                        
                                        <td > 
                                          <input type="checkbox"  :name="index+'row'" :id="index+'row'"  :checked="selected_download[index].row" v-model="selected_download[index].row" @change = "sel_dwn_prod($event,index)" >                              
                                          {{index+1}} </td>
                                        <td>{{product.brand_desc}}</td>
                                        <td>{{product.product_name}}</td>
                                        <td>
                                                <input type="checkbox"  :name="index+'2d'" :id="index+'2d'"  v-if="!(selected_download[index].d_2d == status_hide)"  :checked="selected_download[index].d_2d" v-model="selected_download[index].d_2d">                                 
                                        </td>
                                        <td>
                                                <input type="checkbox" :name="index+'3d'" :id="index+'3d'"   v-if="!(selected_download[index].d_3d == status_hide)" :checked="selected_download[index].d_3d"  v-model="selected_download[index].d_3d">                            
                                        </td>
                                        <td>
                                                <input type="checkbox" :name="index+'pdf'" :id="index+'pdf'" :checked="selected_download[index].d_pdf"  v-model="selected_download[index].d_pdf"> 
                                        </td>                            
                                        </tr>
                                    </tbody>
                                  </table>
                              </div>
                          </div>
                           <div class="text-center p-3">
                              <button type="button" class="btn btn-primary" @click = "downloadZip" >DOWNLOAD NOW</button> 
                          </div>
                    </div>
                    <div class="modal-footer">
                    </div>
                </div>
            </div>
        </div>  
          <div class="row">
            <div class="col-md-3">
                <app-side-nav class="d-none d-md-block pt-3" v-on:callProductList = "getProductList"></app-side-nav>
            </div>
            <div class="col-md-9 container-body">
              <div class="row"> 
              </div>
              <div class="row">
                <div class="col-md-4 p-status_hide"  v-for="(product, i)  in productList.list" :key="product.brand_id">
                  <app-product-card  :in_data="product" :pos="i"  v-on:addProductList = "addProductWatchList" v-on:removeProductList = "removeProductWatchList"></app-product-card>
                </div>
              </div>            
            </div>
          </div>
    </div>
</template>

<script>
// components
import ProductCard from '@/components/common/product-card'
import sideNav from '@/components/layout/side-nav'
import smSideNav from '@/components/layout/sm-side-nav'
import ProductService from '../../shared/services/product.service'
import carousel from 'vue-owl-carousel'

const productService = new ProductService();


export default {
  name: 'Home',
  components: {
    'app-product-card': ProductCard,
    'app-side-nav': sideNav,
    'app-sm-side-nav': smSideNav,
    'carousel': carousel,
  },
  props: {
    msg: String,
    listData:Object,
  },
   data() {
      return {
        productWatchList:[],
        selected_download:[],
        productList:{},
        sc_id:0,
        mc_id:0,
        select_collection:"My Collection",
        status_hide : 2  //info: used to hide the check box while download
      }
    },
  methods: {
      getProductList(mc_id,sc_id,sel_name) {
        productService.getProductList(mc_id,sc_id)
        .then(response => {
          this.productList = response.data.output.success[0].data;
        })
        .catch(e => {
          this.errors.push(e)
        }) 
        
        this.setCollection(sel_name);
      },

      addProductWatchList(product,i){
        let index =  this.productWatchList.findIndex(obj => obj.product_id == product.product_id);
        console.log(product.threed_file_url,"   ",product.twod_file_url );

        // info: check if 3d or 2d file exists
        let status_2d = (product.twod_file_url)? true: this.status_hide;
        let status_3d = (product.threed_file_url)? true: this.status_hide;

        if (index === -1){
          this.selected_download.push({
              'product_id' : product.product_id,
              'row' : true,
              'd_2d' : status_2d,
              'd_3d' : status_3d,
              'd_pdf' : true
              });
          // product['d_2d'] = false;
          // product['d_3d'] = false;
          // product['d_pdf'] = false;
          this.productWatchList.push(product)
          this.productList.list[i]['selected'] = 1;

        }

      },

      removeProductWatchList(product,i){
        let index =  this.productWatchList.findIndex(obj => obj.product_id == product.product_id)

        if (index > -1) {
          this.productWatchList.splice(index, 1);
          this.productList.list[i]['selected'] = 0;

        }

      },

         
      downloadZip(){

          let obj = {"product_list" : this.selected_download};



          productService.downloadZip(obj)
          .then( async response => {


            const blob = new Blob([response.data], { type: 'application/zip' })
            const link = document.createElement('a')
            link.href = URL.createObjectURL(blob)
            link.download = "kworktech.zip"
            link.click()
            URL.revokeObjectURL(link.href)

          })
          .catch(e => {
              this.toast.error("Error occured while adding",{
                  position:'bottom-right'
              })  
                this.errors.push(e)
          })

      },

      sel_dwn_prod(e,pos){
        let status = e.target.checked;

        if(this.selected_download[pos].d_2d != this.status_hide) 
          this.selected_download[pos].d_2d = status;

        if(this.selected_download[pos].d_3d != this.status_hide) 
          this.selected_download[pos].d_3d = status;

          this.selected_download[pos].d_pdf = status ;
      },

      setCollection(sel_name){
        this.select_collection = sel_name;
      }
  },
  
  created() {
    productService.getProductList(this.mc_id,this.sc_id)
    .then(response => {
    this.productList = response.data.output.success[0].data;
      if(this.productList.new){
        this.productList.new.forEach(element => {
            element['product_status']='new' 
        });
      }
    console.log("this.productDEtails product details",this.productList)
    })
    .catch(e => {
    this.errors.push(e)
    })  
  }
}

</script>
<style  scoped>
  template{
    padding:2%;
  }
  .watchlist{
    text-transform:uppercase;
  }
  .watchlistcount{
    color: #007bff;
  }
  .watchlist-block{
    float: right;
    margin-right: 5px;
  }
  .watchlist-exist{
    color:#000000
  }
  .watchlist-none{
    color: #858585
  }
  a, a:hover{
    color:#000000
  }
  .container-body{
    overflow-y: auto;
    /* height: fit-content; */
    height: 610px;
    overflow-x: hidden;
  }
  .container-body::-webkit-scrollbar { 
    display: none; 
  } 
  .logo{
    width:120%;
    margin-left:-10%;
    min-height:60%;
    margin-top:5%;
  }
  .font-weight-bold{
    font-weight: 500 !important;   
}
</style>