<template>
    <div class="product container p-3 mx-auto">
         <div class="row pt-1">
                <div class="col-md-3 d-none d-md-block">
                    <img class="logo" src="@/assets/KWorkTech_new_logo.jpg">
                </div>
          </div>
          <div class="row border-bottom border-dark font-weight-bold pb-1 ">
                <app-sm-side-nav class="d-block d-md-none pl-3"></app-sm-side-nav>
                  <div class="offset-md-4 header-options p-2">
                    <header-options 
                            v-bind:in_data="headerOptions">
                    </header-options>
                </div>
          </div>
        <div class="row">
            <div class="col-md-3">
                <app-side-nav class="d-none d-md-block pt-3" :categoryList1="categoryList"></app-side-nav>
            </div>
            <div class="col-md-9 pt-5" v-if="isCreate">
                <section>
                    <div class="">
                        <div class="h4 pb-4 text-uppercase" >
                            Add Sub Category
                        </div> 
                        <div class="">
                            <div class="row pt-5">
                                <label class="col"> CATEGORY</label> 
                                <select class="col" v-model="AddSubcategory.main_category_id">
                                    <option :value="undefined" selected disabled>Select</option>
                                    <option v-for="(item,index) in categoryList" :value="item.main_category_id" :key="index">{{ item.main_category_name }}</option>
                                </select>
                            </div>
                            <div class="row pt-3">
                                <label class="col"> NEW SUB CATEGORY</label>
                                <input class="col" v-model="AddSubcategory.sub_category_name" placeholder="Enter the name">
