                 <footer class="p-4 text-center">
                    <button  class="btn btn-primary" data-toggle="modal" data-target="#isAddSubCategory">Add</button>
                </footer>
                <div class="modal fade" id="isAddSubCategory" role="dialog">
                    <div class="modal-dialog modal-md">
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" data-dismiss="modal">&times;</button>
                                <h4 class="modal-title">Add new Sub category</h4>
                            </div>
                            <div class="modal-body">
                                <p class="body-text"> Do you confirm to add the sub category?</p>
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>
                                <button type="button" class="btn btn-primary" @click="createSubCategory()" data-dismiss="modal">YES</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
            

            <div class="col-md-9 container-body"  v-if="!isCreate">
                <section>                    
                    <div class="emplate-body tab-bg pt-3">
                        <div class="row">
                            <div class=" h4 col-7 col-md-7 pt-3 pb-4 text-uppercase" >
                                Sub Categories
                            </div>
                            <div class=" col-5 col-md-5 pt-2 pb-2 text-uppercase text-right " >
                                <button type="button" class="btn btn-default text-left text-uppercase font-weight-bold add-button" @click="isCreate=true">
                                    <fa-icon :icon="['fas','plus']"/> Add new 
                                </button>
                            </div>
                        </div>
                        <div class="table-responsive">
                            <table  class="table border pt-5 text-uppercase">
                            <tbody  >
                                <tr>
                                <th>S NO</th>
                                <th>Category</th>
                                <th>Sub Category</th>
                                <th>Edit</th>
                                <th>Status</th>
                                </tr>
                            </tbody>
                            <tbody v-for="(category,index) in categoryList" :key="index">
                                <tr  v-for="(subitem) in category.sub_category" :key="subitem" >
                                    <td>{{subitem.subcategory_id}}</td>
                                    <td>{{category.main_category_name}}</td>
                                    <td>{{subitem.subcategory_name}}</td>
                                    <td>
                                        <button type="button" class="btn btn-default">
                                            <span class="glyphicon glyphicon-plus" @click="editProduct(subitem.subcategory_id)">
                                                <fa-icon :icon="['fas','pencil-alt']"/> 
                                            </span>
                                        </button>
                                    </td>
                                    <td>
                                            <toggle-button :value="active_status" class=" pt-1"
                                            :color ="{checked: '#007bff', unchecked: '#858585'}"
                                            @change="isChangeStatus()"/>
                                        <!-- <button type="button" class="btn btn-default text-right">
                                            <span class="status-title glyphicon glyphicon-plus"  data-toggle="modal" data-target="#isActivateProduct" @click="getProductid(product.product_id,product.active_status)"> 
                                                {{product.active_status == 1 ? 'ACTIVE' : 'INACTIVE'}} </span> 
                                        </button> -->
                                        <!-- <div class="modal fade" id="isActivateProduct" role="dialog">
                                            <div class="modal-dialog modal-md">
                                                <div class="modal-content">
                                                    <div class="modal-header">
                                                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                                                        <h4 class="modal-title"> {{product.active_status == 1 ? 'INACTIVE' : 'ACTIVE'}} the Product</h4>
                                                    </div>
                                                    <div class="modal-body">
                                                        <p class="body-text"> Would you like to {{product.active_status == 1 ? 'INACTIVE' : 'ACTIVE'}} the Product?</p>
                                                    </div>
                                                    <div class="modal-footer">
                                                        <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>
                                                        <button type="button" class="btn btn-primary" @click="activeProduct()" data-dismiss="modal">YES</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div> -->
                                    </td>
                                </tr>
                            
                                </tbody>
                            </table>
                        </div>
                    </div>               
               </section>
            </div> 
        </div>
       
    </div>
</template>

<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
    

    import AddCategoriesService from '../../shared/services/addCategory.service'
    import ProductService from '../../shared/services/product.service'

    import sideNav from '../../components/layout/side-nav.vue'
    import smSideNav from '../../components/layout/sm-side-nav.vue';
    import HeaderOption from '../../components/common/Header-options.vue'

    Vue.use(VueToast)
    const addCategoriesService = new AddCategoriesService();
    const productService = new ProductService();

    // const sidenav = new sideNav();

    export default Vue.extend ({ 
        
    components : {
        'app-side-nav':sideNav,
        'app-sm-side-nav':smSideNav,
        'header-options':HeaderOption
    },
         data() {
            return {
                toast:Vue.$toast,
                productList:{},
                categoryList:{},
                AddSubcategory:{},
                subcategoriesList:[],
                subcategorylength:[],
                isCreate:false,
                active_status:true,
                totalCount:0,
                sc_id:0,
                mc_id:0,
                headerOptions: {
                    createProduct:'true',
                    createCategory:'true',
                    createSubcategory:'false',
                    createBrand:'true'
                }
            }
        },
        methods: {
            getCategory() {
                console.log("category category get category statrts")
                productService.getAdminCategoryList()
                .then(response => {
                    this.categoryList = response.data.output.success[0].data
                    this.categoryList.forEach((element, index) => {
                        var tempObj = JSON.parse(this.categoryList[index].sub_category);
                        this.subcategoriesList[element['sub_category']]=[];
                            for(var i in tempObj){
                                var obj = {};
                                obj['subcategory_id'] = i;
                                obj['subcategory_name'] = tempObj[i];
                                this.subcategoriesList[element['sub_category']].push(obj);
                            }
                        this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                        this.totalCount += this.categoryList[index]['sub_category'].length
                    }); 

                })
                .catch(e => {
                this.errors.push(e)
                })
            },
            createSubCategory(){
                let _subcategoryForm = new FormData()
                    Object.keys(this.AddSubcategory).forEach((key) => {
                        _subcategoryForm.append(key, this.AddSubcategory[key])
                    })
                addCategoriesService.createSubCategory(_subcategoryForm)
                .then(response => {
                   console.log(response);
                   this.AddSubcategory = {};
                    // this.getCategory();
                   
                    this.toast.success("New sub category is added",{
                        position:'top-right',
                        width:'120px'
                   })
                    this.$router.go()
                    // this.getCategory()
                })
                .catch(e => {
                    this.toast.error("Error occured while adding",{
                        position:'top-right'
                   })
                this.errors.push(e)
                })

            },
        },
         created() {
             this.getCategory();
             productService.getAdminCategoryList()
                .then(response => {
                    this.categoryList = response.data.output.success[0].data
                    this.categoryList.forEach((element, index) => {
                        var tempObj = JSON.parse(this.categoryList[index].sub_category);
                        this.subcategoriesList[element['sub_category']]=[];
                            for(var i in tempObj){
                                var obj = {};
                                obj['subcategory_id'] = i;
                                obj['subcategory_name'] = tempObj[i];
                                this.subcategoriesList[element['sub_category']].push(obj);
                            }
                        this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                        // console.log("categorylist category list categorylist",this.categoryList)
                    }); 
                })
                .catch(e => {
                this.errors.push(e)
                })
        }   
    })
</script>

