<template>
    <div class="product container" id="product" >
        <span class="closebtn" @click="$router.go(-1)">&times;</span>
        <div ref="content">
            <section>
                <div class="row border">
                    <img :src="productDetails.hero_img"  :alt="productDetails.product_name" class="w-100">
                </div>
            </section>
            <section>
                <div class="row border p-2">
                    <div class="col" >
                        <div class="row">
                            <div class="col col-md-12 p-0"  v-for="(product, index) in productDetails.images" :key="index">
                                <img v-bind:src="product.product_images_url"  :alt="productDetails.product_name" @click="updateImg(index)" class="w-100 p-2"/> 
                            </div>
                        </div>
                    </div> 
                    <div class="col-12 col-md-9 min-w-80 p-0">
                        <img :src="productDetails.images[product_index].product_images_url" class="w-100 p-2">
                    </div>
                </div> 
            </section>
             <section>
                <div class="row">
                    <div class="col p-0 border">
                        <div class="p-2 border-bottom">
                            <div class="p-2 title">
                                Description
                            </div>
                            <div class="col p-2 content">
                                {{productDetails.product_desc}}
                            </div>
                        </div>
                        <ul class="list-group">
                            <li class="list-group-item title border-bottom ">Features
                                <ul class="pl-4 pt-4">
                                    <li class="content" v-for="(feature,index) in productDetails.features" :key="index">
                                        {{feature.features_name}}
                                    </li>
                                </ul>
                            </li>
                            <li class="list-group-item title border-bottom ">BRAND : <span class="item-description">{{productDetails.brand_name}}</span></li>
                            <li class="list-group-item title border-bottom ">PRODUCT : <span class="item-description">{{productDetails.product_name}} </span></li>
                            <li class="list-group-item title border-bottom ">DESIGNER : <span class="item-description">{{productDetails.product_designer}}</span></li>
                            <li class="list-group-item title border-bottom ">SIZE : 
                                <span class="item-description">
                                    <span v-if="!productDetails.multiple_flag"> {{productDetails['dimension_depth']}}[D] * {{productDetails['dimension_height']}} [H] * {{productDetails['dimension_width']}}[w] </span>
                                    <span v-if="productDetails.multiple_flag"> Multiple</span>
                                </span>
                            </li> 
                            <li class="list-group-item title border-bottom "> <a :href="productDetails.third_party_url" target="_blank" rel="noopener noreferrer">LINK TO {{productDetails.product_name}}/{{productDetails.brand_name}}</a></li>
                        </ul>
                        <div class="p-2">
                            <div class="p-2 title">
                                DOWNLOAD
                            </div>
                            <div class="row p-2">
                                <div class="col file-download" v-if="productDetails.threed_file">
                                    <a :href="productDetails.threed_file" download>3D FILE</a>
                                </div>
                                <div class="col file-download"  v-if="productDetails.twod_file">
                                    <a :href="productDetails.twod_file" download>2D FILE</a>
                                </div>
                                <div class="col file-download"> 
                                    <a :href="productDetails.pdf_file_url" download>PDF FILE</a>
                                    <!-- <a :href="baseURL +'products/'+productDetails.product_id+'/'+productDetails.product_name+'/pdf'" download>PDF FILE</a> -->
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col border">
                            <div class="col p-2"> 
                                <div class="title">{{productDetails.brand_name}}</div>
                            </div>
                            <div class="col p-2 content">
                                {{productDetails.brand_desc}}
                            </div>
                            <div class="col p-2">
                                    <img v-bind:src="productDetails.brand_img"  :alt="productDetails.brand_name" class="w-100">
                            </div>
                    </div>
                </div>
            </section>
        </div>
    </div>
</template>

<script lang="ts">
    import Vue from 'vue'
    import jspdf from 'jspdf'
    import html2canvas from "html2canvas"

    // import  pdf from "pdf-creator-node"
    // import * as fs from 'fs-web';
    import * as commonConstant from '../../shared/common.service'
    import ProductService from '../../shared/services/product.service' 
    // import ProductSerddvice from '../../../server/public' 
    
    const productService = new ProductService()

    export default Vue.extend ({  
        name: 'product',
        
        data() {
            return {
                productDetails :{},
                // product_id = Number(this.$route.params.product_id) ,
                product_index:0, 
                baseURL: commonConstant.baseURL,
                itemDetails : {
                    name:'dummy'
                } 
            }
        },
      

        methods: {
            updateImg(index) {
                this.product_index = index;
            },
            download(){
                const doc = new jspdf()
                // const html = this.$refs.content.innerHTML
                // doc.fromHTML(html,15,15,{
                //     width:150
                // })
                var canvasElement = document.createElement('canvas');
                html2canvas(this.$refs.content, { canvas: canvasElement }).then(function (canvas) {
                    const img = canvas.toDataURL("/../../assets/logo.png", 0.8);
                    doc.addImage(img,'png',20,20);
                    // doc.addPage()
                    doc.save("sample.pdf");
                });
            }
        },
        created() {
            const product_id = Number(this.$route.params.product_id) 
            productService.getProductDetails(product_id)
            .then(response => {
                this.productDetails = response.data.output.success[0].data;
            })
            .catch(e => {
                this.errors.push(e);
            })
        },   
    })
</script>

<style  scoped>
  @import '../../style.css';


   .header-bg-image{
       height: 400px !important;
   }

   .closebtn {
        position: absolute;
        top: 0;
        right: 55px;
        font-size: 55px;
        margin-left: 50px;
        cursor: pointer;
    }
    .list-group-item:first-child {
        border-top-left-radius: .0rem !important; 
        border-top-right-radius: .0rem !important;
    }
    .content{
        font-weight: 400 !important;
    }
    .item-description{
        text-transform: inherit;
        font-family: 'Barlow', sans-serif;
        font-size: 24px;
        color:#858585;
        font-weight: 400;
    }
    .file-download{
        font-weight:500
    }
    .list-group-item {
        border:0px;
    }
</style>