<template>
    <div class="product container">
        <div class="row">
                <span class="closebtn" @click="$router.go(-1)">&times;</span>
                <section>
                    <div class="row  border">
                        <input type="file" @change="onMainFileChange($event)" class="w-100 p-2" />
                        <img  :src="main_url" class="w-100  p-2" />
                    </div>
                </section>
                <section>
                    <div class="row border p-2">
                        <div class="col" >
                            <div class="row">
                                <div class="col">
                                    <img  :src="url[0]" class="w-100"/>
                                    <input type="file" @change="onFileChange($event,0)" class="w-100"/>
                                </div>
                                <div class="col">
                                    <img  :src="url[1]" class="w-100"/>
                                    <input type="file" @change="onFileChange($event,1)" class="w-100"/>
                                </div>
                                <div class="col">
                                    <img  :src="url[2]" class="w-100"/>
                                    <input type="file" @change="onFileChange($event,2)" class="w-100"/>
                                </div>
                                <div class="col">
                                    <img  :src="url[3]" class="w-100"/>
                                    <input type="file" @change="onFileChange($event,3)" class="w-100"/>
                                </div>
                                <!-- <div class="col col-md-12 p-0"  v-for="(image,index) in productDetails.images" :key="index">
                                    <input type="file" @change="onFileChange($event,index)" class="w-100 p-2" />
                                    <img  :src="url[index]" class="w-100 p-2" />
                                </div> -->
                            </div>
                        </div> 
                        <div class="col-12 col-md-8 min-w-80 p-0">
                            <img  :src="url[0]" class="w-100 p-2" />
                        </div>
                    </div> 
                </section>
                <section>
                    <div class="row">
                        <div class="col border">
                            <div class="p-2">
                                <div class="p-2 title">
                                    Product Description
                                </div>
                                <div class="content p-2">
                                    <textarea v-model="productDetails.product_desc" placeholder="Enter the product description." maxlength="150" class="w-100"></textarea>
                                </div>
                            </div>
                            <ul class="list-group">
                                <li class="list-group-item title">FEATURES :  <input type="number" class="w-25" v-model="features_count" value="2">
                                    <ul class="pl-4 pt-4">
                                        <li class="content" v-for="(n,index) in Number(features_count)" :key="index">
                                            <input type="text" class="w-100 p-1 m-1" :key="n" v-model="product_feature[index]"   placeholder="Enter feature"/>
                                        </li>
                                    </ul>
                                </li>
                                <li class="list-group-item title">CATEGORY
                                    <select class="form-control" v-model="productDetails.sub_category_id">
                                        <option value="" selected disabled>Select</option>
                                        <option v-for="(subitem,subindex) in subcategoryList" :value="subitem.subcategory_id" :key="subindex">{{ subitem.subcategory_name }}</option>
                                    </select>
                                </li>
                                <li class="list-group-item title">SUB CATEGORY
                                    <select class="form-control" v-model="productDetails.sub_category_id">
                                        <option value="" selected disabled>Select</option>
                                        <option v-for="(subitem,subindex) in subcategoryList" :value="subitem.subcategory_id" :key="subindex">{{ subitem.subcategory_name }}</option>
                                    </select>
                                </li>
                                <li class="list-group-item title">BRAND
                                    <select class="form-control" v-model="productDetails.brand_id">
                                        <option value="" selected disabled>Select</option>
                                        <option v-for="(item,index) in brandsList" :value="item.brand_id" :key="index">{{ item.brand_desc }}</option>
                                    </select>
                                </li>
                                <li class="list-group-item title">PRODUCT NAME:
                                     <input v-model="productDetails.product_name">
                                </li>
                                <li class="list-group-item title">DESIGNER NAME: <input v-model="productDetails.product_designer"></li>

                                <!-- <li class="list-group-item title">SIZE : 
                                    <span v-if="!productDetails.multiple_flag">
                                        <input class="w-25" v-model="productDetails.dimension_depth" placeholder="0">[D] * 
                                        <input class="w-25" v-model="productDetails.dimension_height" placeholder="0">[H] * 
                                        <input class="w-25" v-model="productDetails.dimension_width" placeholder="0">[w]
                                    </span>
                                    <input type="checkbox" id="Multiple"  v-model="productDetails.multiple_flag">
                                    <label for="Multiple">Multiple</label>
                                </li> -->
                                <li class="list-group-item title">SIZE :
                                    <input class="pb-2" type="checkbox" id="Multiple"  v-model="productDetails.multiple_flag">
                                    <label class="pl-1" for="Multiple">Multiple</label>
                                    <div class="p-2" v-if="!productDetails.multiple_flag">
                                            <div class="col p-2">
                                                D : <input class="w-25 p-1" v-model="productDetails.dimension_depth" placeholder="0">
                                            </div>
                                            <div class="col p-2">
                                                H : <input class="w-25 p-1" v-model="productDetails.dimension_height" placeholder="0">
                                            </div>
                                            <div class="col p-2">
                                                W : <input class="w-25 p-1" v-model="productDetails.dimension_width" placeholder="0">
                                            </div>
                                    </div>
                                </li>
                                <li class="list-group-item title">LINK TO {{productDetails.product_name}} :
                                     <input class="w-100" v-model="productDetails.third_party_url">
                                </li>
                            </ul>
                            <div class="p-2">
                                <div class="p-2 title">
                                    UPLOAD
                                </div>
                                    <div class="col p-2">
                                        2D FILES :
                                         <input type="file" id="file" ref="file" v-on:change="handleFileUpload($event.target.files,'2dfile')"/>
                                    </div>
                                    <div class="col p-2">
                                        3D FILES :
                                        <input type="file" ref="3dfile" v-on:change="handleFileUpload($event.target.files,'3dfile')">
                                    </div>
                            </div>
                        </div>
                        <div class="col border">
                                <div class="col content">
                                    <div class="title p-2">
                                        About the brand
                                    </div>
                                    <div class="content p-2">
                                        <textarea v-model="productDetails.brand_desc" placeholder="Enter the brand description."  maxlength="250" class="w-100"></textarea>
                                    </div>
                                </div>
                                <div class="col p-2">
                                    <input type="file" @change="onBrandFileChange($event)" class="w-100 p-2" />
                                    <img  :src="brand_url" class="w-100 p-2" />
                                </div>
                        </div>
                    </div>
                </section>
        </div>
        <footer class="p-3 text-center">
            <button  data-toggle="modal" class="btn btn-primary text-center" data-target="#isAddProduct">update Product</button>
        </footer>
        <div class="modal fade" id="isAddProduct" role="dialog">
            <div class="modal-dialog modal-md">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                        <h4 class="modal-title">Update Product</h4>
                    </div>
                    <div class="modal-body">
                        <!-- <p>Would you like to {{equipmentView.equipment_active? 'INACTIVE' : 'ACTIVE'}} the Equipment?</p> -->
                        <p class="body-text"> Would you like to update the product?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>   
                        <button type="button" class="btn btn-primary" @click="updateProduct()" data-dismiss="modal">YES</button>
                    </div>
                </div>
            </div>
        </div>        
    </div>
</template>

<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    import 'vue-toast-notification/dist/theme-default.css';
    import ProductService from '../../shared/services/product.service'

    Vue.use(VueToast)
    const productService = new ProductService();

    export default Vue.extend ({ 
        
    components : {
    },
        data() {
            
            return {
                productDetails :{},
                categoryList:{},
                brandsList:{},
                subcategoriesList:{},
                subcategoryList:[],
                features_count:2,
                url:  new Array(4),
                main_url: null,
                brand_url: null,
                item:null,
                isSubcategory :false,
                product_image : [],
                product_feature : [],
                toast:Vue.$toast
                
            }
        },
        methods: {
            
            onFileChange(e,i) {
               
                const file = e.target.files[0];
                this.product_image[i] = e.target.files.item(0);
                this.productDetails.images[i].product_images_url = URL.createObjectURL(file);
                this.$set(this.url,i, URL.createObjectURL(file));

            },

            handleFileUpload(file, isFile){
                if(isFile == '2dfile')
                this.productDetails.twod_file = file[0].name;
                else if(isFile == '3dfile')
                this.productDetails.threed_file = file[0].name
            },
            
            onMainFileChange(e) {
                const file = e.target.files[0];
                this.productDetails.hero_img = e.target.files.item(0);
                this.main_url = URL.createObjectURL(file);
            },
            
            onFeatureChange(e,i) {

                this.product_feature[i] = e.target.value;

            },
            
            onBrandFileChange(e) {

                const file = e.target.files[0];
                this.productDetails.brand_img = e.target.files.item(0);
                this.brand_url = URL.createObjectURL(file);

            },        

            getCategory() {
                productService.getCategoriesList()
                .then(response => {
                    this.categoryList = response.data.output.success[0].data
                    this.categoryList.forEach((element, index) => {
                        var tempObj = JSON.parse(this.categoryList[index].sub_category);
                        this.subcategoriesList[element['sub_category']]=[];
                            for(var i in tempObj){
                                var obj = {};
                                obj['subcategory_id'] = i;
                                obj['subcategory_name'] = tempObj[i];
                                this.subcategoriesList[element['sub_category']].push(obj);
                            }
                        this.categoryList[index]['sub_category'] = this.subcategoriesList[element['sub_category']]
                        console.log("categorylist",this.categoryList)
                    }); 
                })
                .catch(e => {
                    this.errors.push(e)
                })
            },

            getCategoryIndex(mc_id){
                let getmc_id = this.productDetails.main_category_id
                console.log("categoryId",mc_id)
                console.log("editprodctid",getmc_id)
                this.categoryList.forEach((element) => {
                if(element.main_category_id == mc_id)
                    this.subcategoryList=element.sub_category;
                });
            },

            getBrands(){
                productService.getBrandsList()
                .then(response => {
                    this.brandsList = response.data.output.success[0].data 
                    console.log("getBrandsList is",this.brandsList)
                })
                .catch(e => {
                this.errors.push(e)
                })
            },

            updateProduct() {
                // product images
                // if(this.product_image.length == 0){
                //     // console.log("products iamges images",this.productDetails.images[0].product_images_url)
                //     this.productDetails.product_image_1 = this.productDetails.images[0]
                //     this.productDetails.product_image_2 = this.productDetails.images[1]
                //     this.productDetails.product_image_3 = this.productDetails.images[2]
                //     this.productDetails.product_image_4 = this.productDetails.images[3]
                // }
                // else {
                //     this.productDetails.product_image_1 = this.product_image[0];
                //     this.productDetails.product_image_2 = this.product_image[1];
                //     this.productDetails.product_image_3 = this.product_image[2];
                //     this.productDetails.product_image_4 = this.product_image[3];
                // }
                // // product feature
                // if(this.product_feature.length==0){
                //     let tempfeatures = []
                //     this.productDetails.features.forEach(element => {
                //         tempfeatures.push(element.features_name)
                //     })
                //     this.productDetails.product_features = tempfeatures
                //     console.log("elemtns for feature",this.productDetails.product_features)
                // }                
                // else{
                //     this.productDetails.product_features = this.product_feature;
                // }
                
                //
                this.productDetails.features = [];    
                this.productDetails.product_features = this.product_feature;    

                                     console.log("productDetils",this.productDetails)

                
                //flag
                // if(!this.productDetails.multiple_flag) 
                //     this.productDetails.multiple_flag = false;

                this.productDetails.multiple_flag = (this.productDetails.multiple_flag)? 1 : 0;


                // return true; 
                let _productData  = new FormData();
                for(var key in this.productDetails) {
                    _productData.append(key, this.productDetails[key]);
                } 
                console.log("detail of product details",this.productDetails);
                productService.updateProduct(_productData)
                .then(response => {
                   console.log("hello response hello ",response);
                   this.toast.success("Product details are edited",{
                        position:'top-right'
                    })
                this.$router.push({path:'/admin/product/1'})
                    
                })
                .catch(e => {
                    console.log("error is error error",e)
                    // this.toast.error(e.errors[0].msg,{
                    //     position:'bottom-right'
                    // })
                    this.errors.push(e)
                    console.log("errors erros erros id",this.errors)
                })
            },

            getProductDetails(product_id){
                productService.getProductDetails(product_id)
                .then(response => {
                    this.productDetails = response.data.output.success[0].data;
                    
                    // init
                    for(let i in this.productDetails.features){
                        this.product_feature.push( this.productDetails.features[i].features_name);
                    }
                    this.features_count = this.productDetails.features.length;
                    // init img url
                    this.main_url = this.productDetails.hero_img;
                    this.brand_url = this.productDetails.brand_img;
                    for(let i in this.productDetails.images){
                        this.url[i] = this.productDetails.images[i].product_images_url;
                    }
                    // this.product_feature = this.productDetails.features;
                    console.log("productDetils",this.product_feature)
                    this.getCategoryIndex(this.productDetails.main_category_id)
                })
                .catch(e => {
                    this.errors.push(e);
                })
            }
        },
        
        created() {
            this.getCategory();
            this.getBrands();
            let product_id = Number(this.$route.params.product_id) 
            this.getProductDetails(product_id)
        } 
    })
</script>

<style  scoped>
  @import '../../style.css';

   
</style>