<template>
    <div class="product container p-3 mx-auto">
         <div class="row pt-1">
                <div class="col-md-3 d-none d-md-block">
                    <img class="logo" src="@/assets/KWorkTech_new_logo.jpg">
                </div>
          </div>
          <div class="row  border-bottom border-dark font-weight-bold pb-1 ">
                <app-sm-side-nav class="d-block d-md-none pl-3" v-on:callProductList = "getProductList"></app-sm-side-nav>
                  <div class="offset-md-3 header-options  p-2">
                    <header-options 
                            v-bind:in_data="headerOptions">
                    </header-options>
                </div>
          </div>
        <div class="row">
            <div class="col-md-3">
                <app-side-nav class="d-none d-md-block pt-3" v-on:callProductList = "getProductList"></app-side-nav>
            </div>
            <div class="col-md-9 pt-5"  v-if="isCreate">
                <section>
                    <div class="">
                        <div class="h4 pb-4 text-uppercase" >
                        Add Category
                        </div> 
                        <div class="category-container">
                            <div class="row pt-3">
                               <label class="col"> NEW CATEGORY </label>
                                <input class="col" v-model="addCategories.main_category_name" placeholder="Enter the name">
                            </div>
                        </div>
                    </div>
                </section>
                 <footer class="p-4 text-center">
                    <button data-toggle="modal" class="btn btn-primary"  data-target="#isAddCategory">Add </button>
                </footer>
                <div class="modal fade" id="isAddCategory" role="dialog">
                    <div class="modal-dialog modal-md">
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" data-dismiss="modal">&times;</button>
                                <h4 class="modal-title">Add new Category</h4>
                            </div>
                            <div class="modal-body">
                                <p class="body-text">Do you confirm to add the Category?</p>
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>
                                <button type="button" class="btn btn-primary" @click="createCategory()" data-dismiss="modal">YES</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 


            <div class="col-md-9 container-body"  v-if="!isCreate">
                <section>                    
                    <div class="template-body tab-bg pt-3">
                        <div class="row">
                            <div class=" h4 col-7 col-md-7 pt-3 pb-4 text-uppercase" >
                                Categories
                            </div>
                            <div class=" col-5 col-md-5 pt-2 pb-2 text-uppercase text-right " >
                                <button type="button" class="btn btn-default text-left text-uppercase font-weight-bold add-button" @click="isCreate=true">
                                    <fa-icon :icon="['fas','plus']"/> Add new 
                                </button>
                            </div>
                        </div>
                        <div class="table-responsive">
                            <table  class="table border pt-5 text-uppercase">
                            <tbody  >
                                <tr>
                                <th>S NO</th>
                                <th>Category</th>
                                <th>Edit</th>
                                <th>Status</th>
                                </tr>
                            </tbody>
                            <tbody >
                                <tr  v-for="(category,index) in categoryList" :key="index">
                                <td> <span  class="">{{index+1}} </span></td>
                                <td><span  class="">{{category.main_category_name}}</span></td>
                                <td>
                                    <button type="button" class="btn btn-default">
                                        <span class="glyphicon glyphicon-plus" @click="editProduct(product.product_id)">
                                            <fa-icon :icon="['fas','pencil-alt']"/> 
                                        </span>
                                    </button>
                                </td>
                                <td>
                                        <toggle-button :value="active_status" class=" pt-1"
                                        :color ="{checked: '#007bff', unchecked: '#858585'}"
                                        @change="isChangeStatus()"/>
                                </td>
                                </tr>
                            
                                </tbody>
                            </table>
                        </div>
                    </div>               
               </section>
            </div> 
            
        </div>
       
    </div>
</template>

<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
    
    import AddCategoriesService from '../../shared/services/addCategory.service'
    import ProductService from '../../shared/services/product.service'

    import HeaderOption from '../../components/common/Header-options.vue'

    import sideNav from '../../components/layout/side-nav.vue'
    import smSideNav from '../../components/layout/sm-side-nav.vue';
    
    Vue.use(VueToast)
    const addCategoriesService = new AddCategoriesService();
    const productService = new ProductService();

    export default Vue.extend ({ 
        
    components : {
        'app-side-nav':sideNav,
        'app-sm-side-nav':smSideNav,
        'header-options':HeaderOption
    },
         data() {
            return {
                toast:Vue.$toast,
                productList:{},
                addCategories:{},
                categoryList :{},
                subcategoriesList:{},
                sc_id:0,
                mc_id:0,
                isCreate:false,
                active_status:true,
                headerOptions: {
                    createProduct:'true',
                    createCategory:'false',
                    createSubcategory:'true',
                    createBrand:'true'
                }
            }
        },
        methods: {     
            createCategory(){
                let _categoryForm = new FormData()
                    Object.keys(this.addCategories).forEach((key) => {
                        _categoryForm.append(key, this.addCategories[key])
                    })
                addCategoriesService.createCategory(_categoryForm)
                .then(response => {
                   console.log("response is reponse is",response);
                   this.toast.success("New category is added",{
                        position:'top-right'
                    })
                    this.addCategories={}
                    // this.getProductList(0,0);
                    // this.$router.push({path:'/admin/add-category'})
                    this.isCreate = false;
                })
                .catch(e => {
                    this.toast.error("Error occured while adding",{
                        position:'top-right'
                    })
                    this.errors.push(e)
                })
            }
        },
       
         created() {
            // getCategories() {
                productService.getCategoriesList()
                .then(response => {
                    this.categoryList = response.data.output.success[0].data
                    console.log("categories categories categories",this.categoryList)
                    this.categoryList.forEach((element, index) => {
                        var tempObj = JSON.parse(this.categoryList[index].sub_category);
                        this.subcategoriesList[element['sub_category']]=[];
                            for(var i in tempObj){
                                var obj = {};
                                obj['subcategory_id'] = i;
                                obj['subcategory_name'] = tempObj[i];
                                this.subcategoriesList[element['sub_category']].push(obj);
                            }
                        this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                    }); 
                })
                .catch(e => {
                this.errors.push(e)
                })
            // },
         }
    })
</script>

<style  scoped>
  @import '../../style.css';


   .header-bg-image{
       height: 400px !important;
   }

   .table td, .table th {
        padding: .75rem;
        vertical-align: top;
        border: 1px solid #e5e5e5;
   }

   /* .closebtn {
        position: absolute;
        top: 0;
        right: 55px;
        font-size: 55px;
        margin-left: 50px;
        cursor: pointer;
    } */
    .logo{
        width:50%;
        margin-bottom: -20%;
    }
    .category-container{
        line-height: 50px;
    }
    input{
        line-height: 30px;
    }
    .header-options{
        margin-left: auto;
    }   
    .container-header{
      margin-left: -15px;
    }
    th{
        text-align:center;
    }
    td{
        text-align:center;
    }
    .add-button{
        float: right;
        padding-right:1%;
    }
</style>